import { useState, useEffect, useCallback } from 'react';
import { notifications } from '@mantine/notifications';
import { invoke } from '@tauri-apps/api/tauri';
import {
    Memory,
    MemoryCreationRequest,
    MemoryFilter,
    MemoryStats,
    MemorySearchResult,
    AIImageGenerationRequest,
    VideoGenerationRequest
} from '../types';

export const useMemory = () => {
    const [memories, setMemories] = useState<Memory[]>([]);
    const [loading, setLoading] = useState(false);
    const [stats, setStats] = useState<MemoryStats | null>(null);

    // 加载记忆列表
    const loadMemories = useCallback(async (filter?: MemoryFilter) => {
        setLoading(true);
        try {
            const memories = await invoke<Memory[]>('get_memories', { filter });
            setMemories(memories || []);
        } catch (error) {
            console.error('Failed to load memories:', error);
            notifications.show({
                title: '加载失败',
                message: '无法加载记忆列表',
                color: 'red'
            });
            setMemories([]);
        } finally {
            setLoading(false);
        }
    }, []);

    // 创建记忆
    const createMemory = useCallback(async (request: MemoryCreationRequest): Promise<Memory> => {
        try {
            const newMemory = await invoke<Memory>('create_memory', { request });
            setMemories((prev: Memory[]) => [newMemory, ...prev]);

            notifications.show({
                title: '创建成功',
                message: '记忆已保存',
                color: 'green'
            });

            return newMemory;
        } catch (error) {
            console.error('Failed to create memory:', error);
            notifications.show({
                title: '创建失败',
                message: '无法保存记忆',
                color: 'red'
            });
            throw error;
        }
    }, []);

    // 更新记忆
    const updateMemory = useCallback(async (id: string, updates: Partial<Memory>) => {
        try {
            const updatedMemory = await invoke<Memory>('update_memory', { id, updates });
            setMemories((prev: Memory[]) => prev.map((m: Memory) =>
                m.id === id ? updatedMemory : m
            ));

            notifications.show({
                title: '更新成功',
                message: '记忆已更新',
                color: 'green'
            });
        } catch (error) {
            console.error('Failed to update memory:', error);
            notifications.show({
                title: '更新失败',
                message: '无法更新记忆',
                color: 'red'
            });
        }
    }, []);

    // 删除记忆
    const deleteMemory = useCallback(async (id: string) => {
        try {
            const response = await fetch(`/api/memories/${id}`, {
                method: 'DELETE',
            });

            if (!response.ok) {
                throw new Error('Failed to delete memory');
            }

            setMemories((prev: Memory[]) => prev.filter((m: Memory) => m.id !== id));

            notifications.show({
                title: '删除成功',
                message: '记忆已删除',
                color: 'green'
            });
        } catch (error) {
            console.error('Failed to delete memory:', error);
            notifications.show({
                title: '删除失败',
                message: '无法删除记忆',
                color: 'red'
            });
        }
    }, []);

    // 搜索记忆
    const searchMemories = useCallback(async (query: string): Promise<MemorySearchResult> => {
        try {
            const response = await fetch(`/api/memories/search?q=${encodeURIComponent(query)}`);
            if (!response.ok) {
                throw new Error('Failed to search memories');
            }

            const data = await response.json();
            return data;
        } catch (error) {
            console.error('Failed to search memories:', error);
            throw error;
        }
    }, []);

    // AI 生成图像
    const generateImage = useCallback(async (request: AIImageGenerationRequest) => {
        try {
            const response = await fetch('/api/memories/generate-image', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(request),
            });

            if (!response.ok) {
                throw new Error('Failed to generate image');
            }

            const data = await response.json();
            const imageUrl = data.imageUrl;

            // 更新记忆的图像URL
            await updateMemory(request.memoryId, {
                imageUrl,
                aiGenerated: true
            });

            notifications.show({
                title: 'AI 图像生成完成',
                message: '记忆图像已生成并保存',
                color: 'green'
            });

            return imageUrl;
        } catch (error) {
            console.error('Failed to generate image:', error);
            notifications.show({
                title: '图像生成失败',
                message: 'AI 图像生成遇到问题',
                color: 'red'
            });
            throw error;
        }
    }, [updateMemory]);

    // 生成视频
    const generateVideo = useCallback(async (request: VideoGenerationRequest) => {
        try {
            const response = await fetch('/api/memories/generate-video', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify(request),
            });

            if (!response.ok) {
                throw new Error('Failed to generate video');
            }

            const data = await response.json();
            const videoUrl = data.videoUrl;

            notifications.show({
                title: '视频生成完成',
                message: '记忆视频已生成',
                color: 'green'
            });

            return videoUrl;
        } catch (error) {
            console.error('Failed to generate video:', error);
            notifications.show({
                title: '视频生成失败',
                message: '视频生成遇到问题',
                color: 'red'
            });
            throw error;
        }
    }, []);

    // 获取统计信息
    const loadStats = useCallback(async () => {
        try {
            const response = await fetch('/api/memories/stats');
            if (!response.ok) {
                throw new Error('Failed to load stats');
            }

            const stats = await response.json();
            setStats(stats);
        } catch (error) {
            console.error('Failed to load stats:', error);
            setStats(null);
        }
    }, []);

    // 初始化加载
    useEffect(() => {
        loadMemories();
        loadStats();
    }, [loadMemories, loadStats]);

    return {
        memories,
        loading,
        stats,
        loadMemories,
        createMemory,
        updateMemory,
        deleteMemory,
        searchMemories,
        generateImage,
        generateVideo,
        loadStats
    };
};